@import url('https://fonts.googleapis.com/css2?family=Lora&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik:wght@400;500;600;700&display=swap');
body{
  font-family: 'Lora', serif;
font-family: 'Poppins', sans-serif;
font-family: 'Rubik', sans-serif;
}
:root{
  --red:#d01c28;
  --gray:#2e2e2e;
}
*{
  margin: 0;
}
button{
  all:unset;
  padding: 6px 16px;
  background: var(--red);
  height :32px;
  color: white;
  font-weight: 500;
}
.container{
  max-width: 1280px;
  margin: 0 auto;
}
nav{
  display: flex;
  justify-content: space-between;
  height: 72px;
  align-items: center;
}
nav ul {
  display: flex;
  list-style: none;
  gap:24px;
}
.hero {
  display: flex;
  align-items: center;
  height:calc(100vh - 72px);
}
.hero-content{
  display: flex;
  flex-direction: column;
  gap: 36px;
}
.hero h1{
  font-weight: 800;
  font-size: 108px;
  line-height: 102px;
}
.hero p{
  font-weight: 600;
  color: var(--gray);
  max-width: 400px;
}
.hero-btn{
  display: flex;
  gap: 40px;
}
.hero-btn button{
  font-size: 24px;
}
.secondary-btn{
  background: transparent;
  color: var(--gray);
  border: 1px solid gray;
}
.shoping p{
  font-size: 14px;
  margin-bottom: 8px;
}
.brand-icons {
  display: flex;
  gap:16px;
}